/*******************************************************/
/******************* 21. Page Banner *******************/
/*******************************************************/
.page-banner {
    @include overlay(black, 0.6);
    background-size: cover;
    background-position: center; }
.banner-inner {
    position: relative;
    h1 {
        text-transform: capitalize;
        @include tb-d {
            font-size: 65px; }
        @include mo-d {
            font-size: 45px; }
        @include sm-d {
            font-size: 38px; }
        @include xs-d {
            font-size: 33px; } }
    @include xl-d {
        .bg-text {
            transform: translate(-50px, -50%); } }
    &.text-center {
        .bg-text {
            transform: translate(-50%, -50%); }
        .breadcrumb {
            justify-content: center; } } }
.breadcrumb {
    margin: 0;
    padding: 0;
    font-size: 35px;
    background: transparent;
    text-transform: capitalize;
    @include mo-d {
        font-size: 25px; }
    @include sm-d {
        font-size: 20px; }
    .breadcrumb-item {
        @include flexcenter(center);
        &.active {
            color: white;
            text-decoration: underline;
            text-decoration-thickness: 1px; }
        &+.breadcrumb-item::before {
            @include gapLeftRight(margin, 33px);
            @include circle(white, 10px);
            content: '';
            @include mo-d {
                @include gapLeftRight(margin, 15px); } } } }
