/*******************************************************/
/****************** 10. Marquee Text *******************/
/*******************************************************/
.marquee-text {
    transform: rotate(-2.56deg);
    .slick-list {
        @include gapTopBottom(padding, 35px);
        border-top: 1px solid $border-color;
        border-bottom: 1px solid $border-color; }
    h2 {
        margin: 0 15px;
        font-size: 40px;
        text-transform: uppercase;
        @include mt-d {
            font-size: 35px; }
        @include tb-d {
            font-size: 30px; }
        @include mo-d {
            font-size: 25px; }
        @include sm-d {
            font-size: 20px; } } }
