// GLOBAL MIXINS

@mixin overlay($bg-colour, $opacity) {
    z-index: 1;
    position: relative;
    &::before {
        position: absolute;
        content: "";
        width: 100%;
        height: 100%;
        z-index: -1;
        top: 0;
        left: 0;
        opacity: $opacity;
        background-color: $bg-colour; } }


// Column Gap
@mixin colgap($gap) {
    margin-left: -(calc( $gap / 2 ));
    margin-right: -(calc($gap / 2));
    & > div {
        padding-left: -(calc($gap / 2));
        padding-right: -(calc($gap / 2)); } }


// Make Own Container
@mixin container($width) {
    max-width: $width;
    margin-left: auto;
    margin-right: auto; }


// Flex Center
@mixin flexcenter($justify) {
    display: flex;
    align-items: center;
    justify-content: $justify; }


// Mixing for Size
@mixin size($width, $height: $width) {
    width: $width;
    height: $height; }


// Mixing for Box
@mixin box($bg, $width, $height: $width) {
    width: $width;
    height: $height;
    background: $bg; }

// Mixing for Circle
@mixin circle($bg, $size) {
    width: $size;
    height: $size;
    background: $bg;
    line-height: $size;
    border-radius: 50%;
    text-align: center; }

// Mixing for color & background color % border color
@mixin color($color, $bg, $bdr-color: $color) {
    color: $color;
    background: $bg;
    border-color: $bdr-color; }

// Mixing for clearfix
@mixin clearfix() {
    &:after {
        display: block;
        clear: both;
        content: ""; } }

// Gap Left and Right
@mixin gapLeftRight($property, $value) {
    #{$property}-left: $value;
    #{$property}-right: $value; }

// Gap Top and Bottom
@mixin gapTopBottom($property, $value) {
    #{$property}-top: $value;
    #{$property}-bottom: $value; }


// RESPONSIVE MEDIA MIXINS
// Extra Large Device
@mixin xl-d() {
    @media only screen and (min-width: 1501px) {
        @content; } }

// Project Device
@mixin project-d() {
    @media only screen and (max-width: 1700px) and (min-width: 1200px) {
        @content; } }


// Large Device
@mixin lg-d() {
    @media only screen and (max-width: 1500px) {
        @content; } }

// Medium Device
@mixin md-d() {
    @media only screen and (max-width: 1199px) {
        @content; } }

// Within Medium and Tab Device
@mixin mt-d() {
    @media only screen and (max-width: 1199px) and (min-width: 991px) {
        @content; } }

// Tab Device
@mixin tb-d() {
    @media only screen and (max-width: 991px) {
        @content; } }

// Within Tab and Mobile Device
@mixin tm-d() {
    @media only screen and (max-width: 991px) and (min-width: 768px) {
        @content; } }

// Mobile Device
@mixin mo-d() {
    @media only screen and (max-width: 767px) {
        @content; } }

// Within Mobile and Small Device
@mixin ms-d() {
    @media only screen and (max-width: 575px) {
        @content; } }

// Small Medium Device
@mixin sm-d() {
    @media only screen and (max-width: 480px) {
        @content; } }

// Small Medium Device
@mixin xs-d() {
    @media only screen and (max-width: 375px) {
        @content; } }
